<template>
  <a-layout >
    <a-layout-content style="min-width: 1200px;margin-right: auto;margin-left: auto;padding: 0 50px">
      <a-breadcrumb style="margin: 16px 0">
        <a-breadcrumb-item>首页</a-breadcrumb-item>
        <a-breadcrumb-item>企业资质</a-breadcrumb-item>
        <a-breadcrumb-item>修改基本资料</a-breadcrumb-item>
      </a-breadcrumb>

      <div :style="{ background: '#fff', padding: '24px', minHeight: '280px' }" style="min-width: 1200px;max-width: 1400px;">
        <a-row :gutter="24" type="flex" justify="center" align="middle">
          <a-col :span="1" offset="2">
            <a-card :bordered="false">
              <div class="account-center-avatarHolder">
                <div class="avatar">
                  <img src="../../assets/dddb-webEnt/company1.png" />
                </div>
              </div>
            </a-card>
          </a-col>
          <a-col :span="21">
            <a-form
              @submit="handleSubmit" :form="form">
              <a-form-item
                label="在建工程名称"
                :labelCol="{lg: {span: 7}, sm: {span: 7}}"
                :wrapperCol="{lg: {span: 10}, sm: {span: 17} }"
              >
                <a-input
                  style="width: 100%"
                  placeholder="在建工程名称"
                  name="nowProject"
                  v-decorator="[
            'nowProject',
            {rules: [{ required: false, message: '请输入需求名称' }]}
          ]"
                />
              </a-form-item>
              <a-form-item
                label="经营范围"
                :labelCol="{lg: {span: 7}, sm: {span: 7}}"
                :wrapperCol="{lg: {span: 10}, sm: {span: 17} }"
              >
                <a-input
                  style="width: 100%"
                  placeholder="建筑/基建/商贸/金融服务"
                  name="needsDes"
                  v-decorator="[
            'scope',
            {rules: [{ required: false, message: '请输入需求名称' }]}
          ]"
                />
              </a-form-item>

              <a-form-item
                label="企业介绍"
                :labelCol="{lg: {span: 7}, sm: {span: 7}}"
                :wrapperCol="{lg: {span: 10}, sm: {span: 17} }"
              >
                <a-textarea
                  rows="4"
                  placeholder="企业相关介绍"
                  v-decorator="[
            'enterDes'
          ]"
                />
              </a-form-item>
              <a-form-item :wrapperCol="{ span: 24 }" style="text-align: center">
                <a-button htmlType="submit" type="primary" @click="postBasicInfo">确认修改</a-button>
              </a-form-item>
            </a-form>
          </a-col>
        </a-row>
      </div>
    </a-layout-content>
    <a-layout-footer style="text-align: center">DDDB ©2019 Created by 六芒星</a-layout-footer>
  </a-layout>
</template>

<script>
import {postBasicInfoRequest} from '@/api/login'
export default {
  components: {},
  data() {
    return {
      // form
      id: Number(localStorage.getItem('entId')),
      form: this.$form.createForm(this)
    };
  },
  mounted() {
    this.$parent.showMenu=true
  },
  methods: {
    postBasicInfo() {
      const {
        form: { validateFields }
      } = this;
      // 先校验，通过表单校验后，才进入下一步
      validateFields((err, values) => {
        if (!err) {
          const infoValue = { ...values,id:this.id };
          const that = this;
          postBasicInfoRequest(infoValue)
            .then(function(response) {
              console.log(response);
              if (response.data.result === 25) {
                console.log("ds");
                that.$router.push({ path: "/entProfile" });
              }
              alert("提交成功");
              // this.$router.push('/DemandSucceed')
            })
            .catch(function(error) {
              console.log(error);
            });
        }
      });
    },
    // handler
    handleSubmit(e) {
      e.preventDefault();
      this.form.validateFields((err, values) => {
        if (!err) {
        }
      });
    }
  }
};
</script>

<style lang="less" scoped>
.account-center-avatarHolder {
  text-align: center;
  margin-bottom: 24px;

  & > .avatar {
    margin: 0 auto;
    width: 104px;
    height: 104px;
    margin-bottom: 20px;
    border-radius: 50%;
    overflow: hidden;
    img {
      height: 100%;
      width: 100%;
    }
  }

  .username {
    color: rgba(0, 0, 0, 0.85);
    font-size: 20px;
    line-height: 28px;
    font-weight: 500;
    margin-bottom: 4px;
  }
}

.account-center-detail {
  p {
    margin-bottom: 8px;
    padding-left: 26px;
    position: relative;
  }
}

.card-list {
  /deep/ .ant-card-body:hover {
    .ant-card-meta-title > a {
      color: #f5222d;
    }
  }

  /deep/ .ant-card-meta-title {
    margin-bottom: 12px;

    & > a {
      display: inline-block;
      max-width: 100%;
      color: rgba(0, 0, 0, 0.85);
    }
  }

  /deep/ .meta-content {
    position: relative;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    height: 64px;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;

    margin-bottom: 1em;
  }
}

.card-avatar {
  width: 48px;
  height: 48px;
  border-radius: 48px;
}

.ant-card-actions {
  background: #f7f9fa;

  li {
    float: left;
    text-align: center;
    margin: 12px 0;
    color: rgba(0, 0, 0, 0.45);
    width: 50%;

    &:not(:last-child) {
      border-right: 1px solid #e8e8e8;
    }

    a {
      color: rgba(0, 0, 0, 0.45);
      line-height: 22px;
      display: inline-block;
      width: 100%;
      &:hover {
        color: #f5222d;
      }
    }
  }
}

.new-btn {
  background-color: #fff;
  border-radius: 2px;
  width: 100%;
  height: 188px;
}

.vcenter {
  position: absolute;
  left: 50%;
  top: 50%;
}
</style>



